.popup__title {
  font-family: 'Inter', ‘Arial’, sans-serif;
  font-style: normal;
  font-weight: 900;
  font-size: 24px;
  line-height: 1.25;
  color: #000000;
  width: 100%;
  margin: 0;
  margin: 0 0 54px;
}

@media screen and (max-width: 767px) {
  .popup__title {
    font-size: 18px;
    line-height: 1.22;
  }
}
